import React from "react";
import Paper from "@material-ui/core/Paper";

export const FishPanel = props => {
  const { fish } = props;
  return (
    <Paper className={"fishPanel"}>
      <h3>{fish.name_cn}</h3>
      <p className={"timeText"}>
        {fish.time.is_all_day
          ? "全天"
          : `${fish.time.times[0].time_start}:00 - ${fish.time.times[0].time_end}:00`}
      </p>
      <div class="shadow-size">影子型号：{fish.shadow_size.toLocaleString()} </div>
      <div>{fish.price.toLocaleString()} 铃钱 </div>
      <div className={"fishimg"}>
        <img src={`./fish/${fish.img}`} alt=""></img>
      </div>
      {fish.rainingOnly && (
        <div>
          🌧️<em>雨天 </em> 🌧️
        </div>
      )}
    </Paper>
  );
};
